/**
 *             
 * @param {Number} iNumber       
 * @param {Array<String>} aEndings Array       (1, 4, 5),  ['', '', '']
 * return {String}
 */
export default function getNumEnding(iNumber, aEndings) {
  iNumber = iNumber % 100;
  if (iNumber >= 11 && iNumber <= 19) return aEndings[2];
  const i = iNumber % 10;
  switch (i) {
    case (1):
      return aEndings[0];
    case (2):
    case (3):
    case (4):
      return aEndings[1];
    default:
      return aEndings[2];
  }
}